.kr {
  width: 100%;
  height: 10em;
  display: grid;
  grid-template-columns: 1% 99%;
  grid-template-rows: 5em 5em;
  grid-template-areas:
    "statusBar header"
    "statusBar body";
  background-color: rgb(253, 253, 253);
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);

  border-radius: 1em;
  margin-bottom: 0.5em;
}

.kr-statusBar {
  grid-area: statusBar;
  width: 100%;
  height: 10em;
}
.statusBar-low {
  width: 100%;
  height: 100%;
  background-color: #009fe3;
  border-radius: 1em 0 0 1em;
}
.statusBar-medium {
  width: 100%;
  height: 100%;
  background-color: #0099b7;
  border-radius: 1em 0 0 1em;
}
.statusBar-high {
  width: 100%;
  height: 100%;
  background-color: #006d99;
  border-radius: 1em 0 0 1em;
}

.kr-header {
  width: 100%;
  height: 5em;
  padding-top: 1em;
  grid-area: header;
  display: flex;
  justify-content: space-between;
}

.kr-header-title {
  width: 55%;
  padding-left: 1em;
}
.kr-header-title h3 {
  font-size: 1.3rem;
}

.kr-header-buttons {
  width: 10%;
  display: flex;
  justify-content: space-evenly;
  margin-right: 1em;
}

.kr-header-icons {
  cursor: pointer;
  font-size: 1.5em;
  color: #87008b;
}
.kr-header-icons:hover{
  color: #00335E;
}


.kr-header-rating {
  width: 40%;
  display: flex;
  justify-content: flex-end;
  padding-right: 2em;
}
.kr-header-rating h4 {
  width: 5em;
  text-align: center;
}

.kr-body {
  width: 100%;
  height: 5em;
  grid-area: body;
  display: flex;
  justify-content: space-between;
}

.kr-body-owner {
  width: 15em;
  height: 100%;
  padding-left: 1em;
  display: flex;
  align-items: flex-end;
  padding-bottom: .5em;
}

.kr-body-type {
  width: 10em;
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.kr-body-status {
  width: 4em;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 0.8em;
  padding-bottom: .5em;
  margin-top: -2em;
}
.kr-body-status span{
  color: #006d99;
  font-size: 1rem;
  font-weight:bold;
  text-align: left;
  border-bottom: 2px solid #89898934;
  margin-bottom: .5em;
  
}
.kr-body-status h4 {
  width: 4em;
  border-radius: 0.8em;
  padding: 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.status-yellow {
  font-size: 1rem;
  background-color: rgba(255, 255, 0, 0.842);
}
.status-gree {
  font-size: 1rem;
  background-color: rgba(32, 231, 32, 0.712);
}
.status-red {
  font-size: 1rem;
  background-color: rgba(255, 0, 0, 0.678);
}

/* Button learn more  aqui*/

